<script>
    export default {
        name: "StoreDetails",
        data(){
            return{
                pId:0,
                shop:[],
                storeInfo:[],
                foodList:[],
                listArr:[],
                evaluateArr:[],
                pingjiaArr:[],
                category_list:[],
                category_list2:[],
                changeLeftstyle:0,
                isShow:true,
                isShow2:false,
                is_activity_show1:true,
                is_activity_show2:false,
                menu_left_li_dian:true,
                tag_list_ul_li_bian:true,
                // storedetails:[]
                // is_list_dianji2:false,
                newprice:0,
                oldprice:0,
                //选中的所有商品,为去从但为使数量加一
                xuanzhong:[],
                //选中的商品,过滤,数量加一
                proArr:[],
                count:0,
                price:0,
                fale:false
            }
        },
        created(){
            this.shop = JSON.parse(localStorage.getItem("shop"));
            // this.pId = this.$route.query.pId;
            // console.log(this.pId);
            this.axios.get("shopping/restaurant/"+this.pId).then(res=>{
                this.storeInfo = res;
            }).catch(err=>{});
            this.axios.get("/shopping/getcategory/" + this.shop.id).then(res=>{
                console.log(res);
                this.category_list = res.category_list;
                this.category_list2 = res.category_list;
                console.log(res.category_list[0].foods[0].attributes[0].icon_name)
                // console.log(res.category_list[0].foods)
            }).catch(err=>{});
            // this.axios.get("shopping/v2/menu?restaurant_id=1").then(res=>{
            //     this.foodList = res;
            //     console.log(this.foodList);
            // }).catch(err=>{});
            this.axios.get("ugc/v2/restaurants/"+this.shop.id+"/ratings/tags").then(res=>{
                this.evaluateArr = res;
                console.log(this.evaluateArr);
            }).catch(err=>{});
            this.axios.get("ugc/v2/restaurants/"+this.shop.id+"/ratings?offset=0&limit=10").then(res=>{
                this.pingjiaArr = res;
                console.log(this.pingjiaArr);
            }).catch(err=>{});
        },
        methods:{
            listyle(i){
                this.changeLeftstyle = i;
                this.category_list2 = [];

                for(let j=i;j < this.category_list.length; j++){
                    this.category_list2.push(this.category_list[j]);

                }
                this.menu_left_li_dian = index;
            },
            chooselist(fArr,index){
                this.listArr = fArr;
                console.log(this.listArr);
                this.menu_left_li_dian = index;
            },
            chooseType(){
                this.isShow = !this.isShow;
                this.isShow2 = !this.isShow2;
                this.is_activity_show1 = !this.is_activity_show1;
                this.is_activity_show2 = !this.is_activity_show2;
            },
            chooseType2(){
                this.isShow = !this.isShow;
                this.isShow2 = !this.isShow2;
                this.is_activity_show1 = !this.is_activity_show1;
                this.is_activity_show2 = !this.is_activity_show2;
            },
            bianyanse(index){
                this.tag_list_ul_li_bian = index;
            },
            add_pro(p){
                this.fale = !this.fale;
                if (this.proArr.length == 0){
                    const obj = {pro:p,count:1};
                    this.proArr.push(obj)
                }else{
                    //添加过,未添加过
                    let filterArr = this.proArr.filter(v=>{
                        return v.pro.name == p.name;
                    });
                    // console.log(filterArr);
                    if (filterArr.length != 0){
                        filterArr[0].count++;
                    }else{
                        const obj = {pro:p, count:1};
                        this.proArr.push(obj);
                    }
                }

                //价格
                this.newprice = p.specfoods[0].price;
                this.oldprice = this.newprice + this.oldprice;
                this.newprice = 0;
            },
            odder(){
                if (this.oldprice >= 20){
                    this.$router.push({path:'/order',query:{
                            pid:this.pId,
                            proArr:this.proArr
                        }}).catch(err=>{});
                    // this.$parent.sendorder(this.proArr);
                }

            }
        }
    }
</script>

